import React from 'react';
import App, { Container } from 'next/app';
import { Provider } from '../components/Context';

export default class MyApp extends App {
  state = {
    projects: {},
  };

  render() {
    const { Component } = this.props;
    return (
      <Container>
        <Provider value={this.state.projects}>
          <Component />
        </Provider>
      </Container>
    );
  }
}
